import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest, handleRequest, service }) => ({
  /**
   * @name Intents#showTransactionDetails
   * @description The intent to show the transactions details
   * @type {function}
   * @inner
   */
  showTransactionDetails: createRequest(Intent.SHOW_TRANSACTION_DETAILS),

  /**
   * @name Intents#initiatePayment
   * @description The intent to initiate a new payment
   * @type {function}
   * @inner
   */
  initiatePayment: createRequest(Intent.INITIATE_PAYMENT),

  /**
   * @name Intents#showTransactionList
   * @description Handles the intent to show the transaction list
   * @type {function}
   * @inner
   */
  showTransactionList: handleRequest(Intent.SHOW_TRANSACTION_LIST, product => {
    if (product) {
      service.setSelectedProduct(product);
    } else {
      throw new Error(
        'Product is undefined. Please pass a product object as the payload of the intent'
      );
    }
  }),
});



