/**
 * @module ext-bbm-transactions-list-ng
 *
 * @description
 * Transaction list view for the widget-bbm-transactions-ng
 *
 * NOTE: Starting from version 2.0.0 this extension (ext-bbm-transactions-list-ng)
 * is only compatible with widget-bbm-transactions-ng (mobile widget).
 * Please use version 1.x.x if you are using widget-bb-transactions-ng.
 *
 * @example
 * <!-- transactions widget model.xml -->
 * <property name="extension" viewHint="text-input,admin">
 *  <value type="string">ext-bbm-transactions-list-ng</value>
 * </property>
 */
import uiBbFormatAmountKey from 'ui-bb-format-amount-ng';
import uiBbmScrollKey from 'ui-bbm-scroll-ng';
import uiBbI18nKey from 'ui-bb-i18n-ng';

import extEvents from './events';
import extHelpers from './helpers';
import extIntents from './intents';

export const events = extEvents;
export const helpers = extHelpers;
export const intents = extIntents;

export const dependencyKeys = [
  uiBbFormatAmountKey,
  uiBbI18nKey,
  uiBbmScrollKey,
];



