/* global window */
import debitCreditSign, { negativeSignKey } from './debit-credit-sign';
import productKindView from './product-kind-view';

/**
 * Mobile Platform enum
 * @name Platform
 * @type {Object}
 * @inner
 */
const Platform = {
  ANDROID: 'android',
  IOS: 'ios',
};

/**
 * @name getPlatform
 * @type {function}
 * @description
 * Returns the mobile platform that is stored on the window object
 * @returns {string} platform
 * @inner
 */
const getPlatform = () => window.BB_PLATFORM;

/**
 * @name warn
 * @type {function}
 * @description
 * Logs a warning message to the console
 * @inner
 */
const warn = msg => console.warn(`[ext-bbm-transactions-list-ng] ${msg}`);

/**
 * @name getSelectedProductToDisplay
 * @description
 * Processes a product.
 *
 * @type {function}
 * @param {Object} product The original product from the API
 * @returns {Object} The processed product
 */
const getSelectedProductToDisplay = product => product && productKindView(product);

/**
 * @name groupTransactions
 *
 * @description
 * Groups transactions by date
 * We assume that scheduledDate is always date only, e.g. "2016-10-14"
 * Otherwise this method should be improved
 *
 * @type {function}
 * @param {Array<object>} transactions
 * @returns {Array} groups - Transactions grouped by date
 * @inner
 */
const groupTransactions = transactions => {
  const groups = [];

  transactions.forEach(transaction => {
    const date = transaction.bookingDate;
    let group = groups.find(currGroup => currGroup.date === date);

    if (!group) {
      group = {
        date,
        transactions: [],
      };
      groups.push(group);
    }

    group.transactions.push(transaction);
  });

  return groups;
};

/**
 * @name getTransactionsToDisplay
 * @description
 * Processes the list of transactions.
 *
 * @type {function}
 * @param {Array<object>} transactions The original transactions from the API
 * @returns {Array<object>} The list of processed transactions
 */
const getTransactionsToDisplay = transactions => (
  groupTransactions(transactions.map(debitCreditSign))
);

/**
 * @description
 * Helpers for ext-bbm-transactions-list-ng.
 *
 * @name Helpers
 * @type {Object}
 */
export default ({ service, stateContainer, intents }) => {
  /**
   * @description
   * Returns the current selected `product` from the state
   *
   * @name Helpers#getSelectedProduct
   * @type {function}
   * @returns {Object} product Product from the state
   * @inner
   */
  const getSelectedProduct = stateContainer.createSelector(
    state => getSelectedProductToDisplay(state.product.data)
  );

  /**
   * @description
   * Returns the `transactions` from the state
   *
   * @name Helpers#getTransactions
   * @type {function}
   * @returns {Array} transactions List of transactions from the state
   * @inner
   */
  const getTransactions = stateContainer.createSelector(
    state => getTransactionsToDisplay(state.transactions.data)
  );

  /**
   * @description
   * Returns true if there are `transactions` on the state. Otherwise returns false.
   *
   * @name Helpers#hasTransactions
   * @type {function}
   * @returns {boolean}
   * @inner
   */
  const hasTransactions = stateContainer.createSelector(state => Boolean(
    state.transactions.data &&
    state.transactions.data.length > 0
  ));

  /**
   * @description
   * Returns the searched `transactions` from the state
   *
   * @name Helpers#getSearchTransactions
   * @type {function}
   * @returns {Array} transactions List of transactions from the state
   * @inner
   */
  const getSearchTransactions = stateContainer.createSelector(
    state => getTransactionsToDisplay(state.search.data)
  );

  /**
   * @description
   * Returns true if there are searched `transactions` on the state. Otherwise returns false.
   *
   * @name Helpers#hasSearchTransactions
   * @type {function}
   * @returns {boolean}
   * @inner
   */
  const hasSearchTransactions = stateContainer.createSelector(state => Boolean(
    state.search.data &&
    state.search.data.length > 0
  ));

  /**
   * @description
   * Returns true if there is a `product` on the state. Otherwise returns false.
   *
   * @name Helpers#hasSelectedProduct
   * @type {function}
   * @returns {boolean}
   * @inner
   */
  const hasSelectedProduct = stateContainer.createSelector(state => state.product.data !== null);

  /**
   * @description
   * Checks for initial loading.
   *
   * @name Helpers#showLoadingState
   * @type {function}
   *
   * @returns {boolean}
   */
  const showLoadingState = stateContainer.createSelector(state => Boolean(
    state.transactions.loading &&
    !state.transactions.data &&
    !state.transactions.error
  ));

  /**
   * @description
   * Checks whether the searching flag on the state is set to true
   *
   * @name Helpers#isSearching
   * @type {function}
   * @returns {boolean}
   */
  const isSearching = stateContainer.createSelector(state => state.searching);

  /**
   * @description
   * Checks whether the widget is running on Android.
   * Returns true, if the platform is Android, false otherwise.
   *
   * @name Helpers#isAndroid
   * @type {function}
   * @returns {boolean}
   */
  const isAndroid = () => getPlatform() === Platform.ANDROID;

  /**
   * @description
   * Checks whether the widget is running on iOS.
   * Returns true, if the platform is iOS, false otherwise.
   *
   * @name Helpers#isIOS
   * @type {function}
   * @returns {boolean}
   */
  const isIOS = () => getPlatform() === Platform.IOS;

  /**
   * @name Helpers#showList
   * @type {function}
   * @description
   * Determines if the list view should be shown
   * @return {boolean}
   * @inner
   */
  const showList = stateContainer.createSelector(state => !state.searching);

  /**
   * @name Helpers#showSearch
   * @type {function}
   * @description
   * Determines if the search view should be shown
   * @return {boolean}
   * @inner
   */
  const showSearch = stateContainer.createSelector(state => state.searching);

  /**
   * @description
   * Determines if the search error view should be shown
   *
   * @name Helpers#showSearchErrorState
   * @type {function}
   *
   * @returns {boolean}
   */
  const showSearchErrorState = stateContainer.createSelector(state => Boolean(
    state.searching &&
    state.search.error &&
    !state.search.loading
  ));

  /**
   * @description
   * Determines if the search empty state view should be shown
   *
   * @name Helpers#showSearchEmptyState
   * @type {function}
   *
   * @returns {boolean}
   */
  const showSearchEmptyState = stateContainer.createSelector(state => Boolean(
    state.searching &&
    Boolean(state.search.data && !state.search.data.length > 0) &&
    !state.search.error &&
    !state.search.loading
  ));

  /**
   * @description
   * Determines if the search empty state view should be shown
   *
   * @name Helpers#showSearchLoadingState
   * @type {function}
   *
   * @returns {boolean}
   */
  const showSearchLoadingState = stateContainer.createSelector(state => Boolean(
    state.searching &&
    state.search.loading &&
    !state.search.data &&
    !state.search.error
  ));

  /**
   * @description
   * Determines if the empty state view should be shown
   *
   * @name Helpers#showEmptyState
   * @type {function}
   *
   * @returns {boolean}
   */
  const showEmptyState = stateContainer.createSelector(state => Boolean(
    !state.searching &&
    Boolean(state.transactions.data && !state.transactions.data.length > 0) &&
    !state.transactions.loading &&
    !state.transactions.error
  ));

  /**
   * @description
   * Determines if the error state view should be shown
   *
   * @name Helpers#showErrorState
   * @type {function}
   *
   * @returns {boolean}
   */
  const showErrorState = stateContainer.createSelector(state => Boolean(
    !state.searching &&
    !state.transactions.loading &&
    state.transactions.error
  ));

  /**
   * @description
   * Checks whether actions for a given account should be displayed.
   * Returns true, if they should be displayed, or `false` otherwise.
   *
   * @name Helpers#showAccountActions
   * @type {function}
   * @returns {boolean}
   */
  const showAccountActions = () => Boolean(
    getSelectedProduct() &&
    getSelectedProduct().debitAccount
  );

  /**
   * @description
   * Checks whether FAB (Floating Action Buttons) should be displayed. Returns true
   * if it should be displayed, false otherwise.
   *
   * @name Helpers#showFAB
   * @type {function}
   * @returns {boolean}
   */
  const showFAB = () => Boolean(
    !isSearching() &&
    isAndroid() &&
    showAccountActions()
  );

  /**
   * @description
   * Checks for the load more's loading state.
   *
   * @name Helpers#showLoadMoreLoading
   * @type {function}
   *
   * @return {boolean}
   */
  const showLoadMoreLoading = stateContainer.createSelector(state => Boolean(
    state.transactions.loading &&
    state.transactions.data
  ));

  /**
   * @description
   * Checks for the load more's loading state.
   *
   * @name Helpers#showSearchLoadMoreLoading
   * @type {function}
   *
   * @return {boolean}
   */
  const showSearchLoadMoreLoading = stateContainer.createSelector(state => Boolean(
    state.searching &&
    state.search.loading &&
    state.search.data
  ));

  /**
   * @description
   * Checks if the load more message should be visible.
   *
   * @name Helpers#showSearchLoadMore
   * @type {function}
   *
   * @return {boolean}
   */
  const showSearchLoadMore = () => (
    showSearchLoadMoreLoading()
  );

  /**
   * @description
   * Checks if the load more message should be visible.
   *
   * @name Helpers#showLoadMore
   * @type {function}
   *
   * @return {boolean}
   */
  const showLoadMore = () => (
    showLoadMoreLoading()
  );

  /**
   * @description
   * Based on credit/debit indicator, put right sign on the transaction amount
   *
   * @name Helpers#getSignedAmount
   * @type {function}
   * @param {Object} transaction Transaction object
   * @returns {number} Signed amount
   */
  const getSignedAmount = transaction => (
    transaction.amount * (transaction.creditDebitIndicator === negativeSignKey ? -1 : 1)
  );

  /**
   * @description
   * Initiates payment from the given product.
   *
   * @name Helpers#initiatePayment
   * @type {function}
   */
  const initiatePayment = () => {
    const product = getSelectedProduct();

    if (!product) {
      warn('Cannot initiate a payment â€“ no selected product');
      return;
    }

    intents.initiatePayment({
      debitAccount: product,
    });
  };

  /**
   * @description
   * Sets the currently selected transactions and fires the intent
   * to show the transaction details
   *
   * @name Helpers#selectTransaction
   * @param {Object} transaction Transaction object
   * @type {function}
   */
  const selectTransaction = transaction => {
    service.setSelectedTransaction(transaction);
    return intents.showTransactionDetails(transaction);
  };

  /**
   * @description
   * Loads more transactions
   *
   * @name Helpers#loadMore
   * @type {function}
   */
  const loadMore = () => service.loadMoreTransactions();

  /**
   * @description
   * Loads more transactions for the active search query
   *
   * @name Helpers#searchMore
   * @type {function}
   */
  const searchMore = () => service.searchMoreTransactions();

  return {
    getSearchTransactions,
    getSelectedProduct,
    getSignedAmount,
    getTransactions,
    hasSearchTransactions,
    hasSelectedProduct,
    hasTransactions,
    initiatePayment,
    isAndroid,
    isIOS,
    loadMore,
    searchMore,
    selectTransaction,
    showAccountActions,
    showEmptyState,
    showErrorState,
    showFAB,
    showList,
    showLoadingState,
    showLoadMore,
    showLoadMoreLoading,
    showSearch,
    showSearchEmptyState,
    showSearchLoadingState,
    showSearchErrorState,
    showSearchLoadMore,
    showSearchLoadMoreLoading,
  };
};



